<template>
  <div class="viproute-box">
    <p class="viproute-tip clearrix">
      我的提现
      <Button class="fr" type="primary" @click="$router.push('/vip/withdraw')">去提现</Button>
    </p>
    <Row class="search-box">
      <Col span="2">记账时间</Col>
      <Col span="6">
        <DatePicker
          type="daterange"
          split-panels
          transfer
          placeholder="选择日期"
          v-model="searchDate"
          @on-change="dateChange"
          separator=" 至 "
          style="width: 200px"
        />
      </Col>
      <Col span="1">状态</Col>
      <Col span="6">
        <Select v-model="status" clearable style="width:200px">
          <Option value="0">提现中</Option>
          <Option value="1">提现成功</Option>
          <Option value="-1">提现失败</Option>
        </Select>
      </Col>
      <Col span="2">
        <Button type="primary" @click="search(1)">查询</Button>
      </Col>
    </Row>
    <Table
      class="mb10"
      :loading="tabLoading"
      stripe
      highlight-row
      :columns="tabColumn"
      :data="recordList"
    ></Table>
    <Page v-if="totalpage > pageSize" :total="totalpage" size="small" @on-change="toPage" />
    <Modal v-model="isDetails" title="提现详情" :mask-closable="false" :footer-hide="true">
      <Form :label-width="200">
        <FormItem label="记账时间：">{{onRecord.createTime}}</FormItem>
        <FormItem label="业务号：">{{onRecord.txSn}}</FormItem>
        <FormItem label="提现前的金额：">{{onRecord.userBeforeTxMoney}}</FormItem>
        <FormItem label="提现金额：">{{onRecord.userTxMoney}}</FormItem>
        <FormItem label="提现后的金额：">{{onRecord.userAfterTxMoney}}</FormItem>
        <FormItem label="提现状态：">{{onRecord.txStatus | status}}</FormItem>
        <FormItem label="订单的描述：">{{onRecord.userDesc}}</FormItem>
        <FormItem>
          <Button type="primary" @click="isDetails = false">确定</Button>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tabLoading: false,
      searchDate: [], //搜索的日期
      status: "", //提现状态 0:提现中 1:提现成功 -1:提现失败 '':所有
      tabColumn: [
        { type: "index", width: 60, align: "center" },
        { title: "记账时间", key: "createTime", width: 160, align: "center" },
        { title: "业务单号", key: "txSn", align: "center" },
        {
          title: "状态",
          key: "txStatus",
          width: 100,
          align: "center"
        },
        { title: "提现金额", key: "userTxMoney", width: 100, align: "center" },
        { title: "结余", key: "userAfterTxMoney", width: 100, align: "center" },
        {
          title: "操作",
          key: "action",
          width: 100,
          align: "center",
          render: (h, params) => {
            return h(
              "Button",
              {
                props: {
                  type: "info",
                  size: "small"
                },
                on: {
                  click: () => {
                    this.showDetails(params.row.txSn);
                  }
                }
              },
              "查看"
            );
          }
        }
      ],
      totalpage: 0, //总页数
      pageSize: 0, //条数
      isDetails: false, //显示提现详情
      recordList: [],
      onRecord: {} //单个记录详情
    };
  },
  filters: {
    //   提现状态 0:提现中 1:提现成功 -1:提现失败
    status(val) {
      switch (val) {
        case 0:
          return "提现中";
          break;
        case 1:
          return "提现成功";
          break;
        case -1:
          return "提现失败";
          break;

        default:
          break;
      }
    }
  },
  created() {
    this.search();
  },
  methods: {
    // 更改查询日期
    dateChange(val, type) {
      this.searchDate = val;
    },
    // 查询
    search(page = 1) {
      console.log(page)
      this.tabLoading = true;
      this.$api
        .withdrawRecord({
          txStatus: this.status || "", //提现状态
          offset: page, //页码
          sTime: this.searchDate[0] || "",
          eTime: this.searchDate[1] || ""
        })
        .then(res => {
          let resData = res.data.data;
          this.recordList = resData.records.map(item => {
            switch (item.txStatus) {
              case 0:
                item.txStatus = "提现中";
                break;
              case 1:
                item.txStatus = "提现成功";
                break;
              case -1:
                item.txStatus = "提现失败";
                break;

              default:
                break;
            }
            return item;
          });
          this.totalpage = resData.total;
          this.pageSize = resData.size;
          this.tabLoading = false;
        })
        .catch(err => console.log(err));
    },
    // 查看单个记录
    showDetails(txSn) {
      this.$api
        .withdrawOneRecord({
          txSn //提现状态
        })
        .then(res => {
          this.onRecord = res.data.data;
          this.isDetails = true;
        })
        .catch(err => console.log(err));
    },
    // 改变页码
    toPage(index) {
      this.search(index);
    }
  }
};
</script>
<style lang="less" scoped>
.search-box {
  line-height: 33px;
  margin-bottom: 30px;
}
</style>



